    font-family: "Lemon-Milk";
    src: url(${___CSS_LOADER_URL_REPLACEMENT_0___});
};
body, html{
    padding: 0;
    margin: 0;
    overflow: hidden;
}

/* body{
    background-size: cover;
    background-position: 22% 23%;
    background-repeat: no-repeat;
} */

body , .body {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    overflow: hidden;
}

#background-video{
    width: 100vw;
    height: 100vh;
    object-fit: cover;
    position: fixed;
    left: 0;
    right: 0;
    top: 0;
    bottom: 0;
    z-index: -1;
}

.search-section {
    margin: 1%
}

.body {
    margin-left: 0%;
    border: 1px solid rgba(70, 70, 70, 0.036);
    border-radius: 5px;
    padding: 2%;
    margin: 4%;
    background-color: rgba(216, 215, 215, 0.833);
    justify-self: center;
}


html > *{
    font-family: "Lemon-Milk";
    color:rgb(70, 70, 70); 
}

.location, .current {
    width: 400px;
    display: flex;
    flex-direction: column;
    padding-left: 7%;
}

p + span {
    margin-left: 10%;
}


h1 {
    font-size: 2.5em;
    margin: 1%;
}


